
import { defineComponent } from "vue";
import { useStore } from "vuex";

import FontStyle from "./FontStyle.vue";

export default defineComponent({
    name: "menu-panel",
    components: {
        FontStyle,
    },
    setup() {
        const store = useStore();
        const option = store.getters.option;

        return {
            model: option,
        };
    },
});
